import React from 'react';
import { Popconfirm, Typography, Space } from 'antd';
import {
  ColumnsProps,
  EditableTableProps,
  RefEditTableProps,
} from './interface';
export type { ColumnsProps, EditableTableProps, RefEditTableProps };

export interface OperationProps {
  multiple?: boolean;
  rowKey: string;
  optConfig: EditableTableProps["optConfig"];
  isEditing: (record: any) => boolean;
  isAddEdit: (record: any) => boolean;
  save: (key: string | number, record: any, indx: number) => Promise<void>;
  isOptDelete?: boolean;
  cancel: (id: string | number) => void;
  onDelete: (id: string | number, rowItem: any, index: number) => void;
  edit: (record: any) => void;
  newAdd: (string | number)[];
  editingKey: (string | number)[];
}

/** Êìç‰ΩúÂàóÈÖçÁΩÆ */
const operation = ({
  optConfig,
  isEditing,
  isAddEdit,
  save,
  isOptDelete,
  cancel,
  onDelete,
  edit,
  newAdd,
  editingKey,
  rowKey,
  multiple,
}: OperationProps): ColumnsProps[] => [
    {
      title: 'Êìç‰Ωú',
      align: 'center',
      width: 120,
      ...optConfig,
      render: (item: any, record: any, index: number) => {
        const editable = isEditing(record);
        const isNewAdd = isAddEdit(record);
        if (optConfig && optConfig.render) {
          return optConfig.render(item, record, index, {
            editable,
            isNewAdd,
            save,
            cancel,
            onDelete,
            edit,
            newAdd,
            editingKey,
          });
        }
        return editable ? (
          <span>
            <Typography.Link
              onClick={() => save(record[rowKey], record, index)}
              style={{ marginRight: 8 }}
            >
              ‰øùÂ≠ò
            </Typography.Link>
            <Popconfirm
              title="ÊòØÂê¶ÂèñÊ∂àÂΩìÂâçË°åÁºñËæë?"
              okText="ÊòØ"
              cancelText="Âê¶"
              onConfirm={
                // Â¶ÇÊûúÊòØÊñ∞Â¢ûÊìç‰ΩúÁöÑÊï∞ÊçÆÔºåËøõË°åÂà§Êñ≠ ÂèñÊ∂àÊó∂‰ΩøÁî®Âà†Èô§ÊñπÊ≥ï
                isNewAdd
                  ? onDelete.bind(this, record[rowKey], record, index)
                  : cancel.bind(this, record[rowKey])
              }
            >
              <Typography.Link>ÂèñÊ∂à</Typography.Link>
            </Popconfirm>
          </span>
        ) : (
          <Space>
            <Typography.Link
              disabled={!!editingKey.length && !multiple}
              onClick={() => edit(record)}
            >
              ÁºñËæë
            </Typography.Link>
            {isOptDelete && (
              <Popconfirm
                title="ÊòØÂê¶Âà†Èô§ÂΩìÂâçË°åÊï∞ÊçÆ?"
                okText="ÊòØ"
                cancelText="Âê¶"
                onConfirm={() => onDelete(record[rowKey], record, index)}
              >
                <Typography.Link>Âà†Èô§</Typography.Link>
              </Popconfirm>
            )}
          </Space>
        );
      },
    },
  ];

export default operation;
