import { Fragment } from 'react';
import MarkdownPreview from '@uiw/react-markdown-preview';
import rehypeRewrite from 'rehype-rewrite';
import { Component } from 'react';
import Code from './Code';
import styles from './index.module.css';
import Footer from '../Footer';

const getCodeStr = (data = [], code = '') => {
  data.forEach((node) => {
    if (node.type === 'text') {
      code += node.value;
    } else if (node.children && Array.isArray(node.children)) {
      code += getCodeStr(node.children);
    }
  });
  return code;
};

export default class Markdown extends Component {
  render() {
    const { editePath } = this.props;
    return (
      <Fragment>
        <MarkdownPreview
          style={{ padding: '20px 26px 40px 26px' }}
          source={this.props.source}
          rehypePlugins={[
            [
              rehypeRewrite,
              {
                rewrite: (node) => {
                  if (
                    node.type === 'element' &&
                    node.tagName === 'pre' &&
                    node.properties['data-type'] === 'rehyp'
                  ) {
                    node.properties['className'].push(styles.rehyp);
                  }
                },
              },
            ],
          ]}
          components={{
            /**
             * bgWhite ËÆæÁΩÆ‰ª£Á†ÅÈ¢ÑËßàËÉåÊôØÁôΩËâ≤ÔºåÂê¶Âàô‰∏∫Ê†ºÂ≠êËÉåÊôØ„ÄÇ
             * noCode ‰∏çÊòæÁ§∫‰ª£Á†ÅÁºñËæëÂô®„ÄÇ
             * noPreview ‰∏çÊòæÁ§∫‰ª£Á†ÅÈ¢ÑËßàÊïàÊûú„ÄÇ
             * noScroll È¢ÑËßàÂå∫Âüü‰∏çÊòæÁ§∫ÊªöÂä®Êù°„ÄÇ
             * codePen ÊòæÁ§∫ Codepen ÊåâÈíÆÔºåË¶ÅÁâπÂà´Ê≥®ÊÑè ÂåÖÂØºÂÖ•ÁöÑÈóÆÈ¢òÔºåÂÆû‰æã‰∏≠ÁöÑ import ‰∏ªË¶ÅÁî®‰∫é Codepen ‰ΩøÁî®„ÄÇ
             */
            code: ({ inline, node, ...props }) => {
              const {
                noPreview,
                noScroll,
                bgWhite,
                noCode,
                codePen,
                codeSandbox,
              } = props;
              if (inline) {
                return <code {...props} />;
              }
              const config = {
                noPreview,
                noScroll,
                bgWhite,
                noCode,
                codePen,
                codeSandbox,
              };
              if (
                Object.keys(config).filter((name) => config[name] !== undefined)
                  .length === 0
              ) {
                return <code {...props} />;
              }
              return (
                <Code
                  code={getCodeStr(node.children)}
                  dependencies={this.props.dependencies}
                  {...{
                    noPreview,
                    noScroll,
                    bgWhite,
                    noCode,
                    codePen,
                    codeSandbox,
                  }}
                />
              );
            },
          }}
        />
        {editePath && (
          <div className={styles.footer}>
            <a href={editePath}>ÁºñËæëÂΩìÂâçÈ°µÈù¢ÂÜÖÂÆπ</a>
          </div>
        )}
        <Footer placement="left" />
      </Fragment>
    );
  }
}
